.accordion {
  width: 100%;
  /* max-width: 1080px; */
  height: 350px;
  overflow: hidden;
  margin: 25px auto;
}

.accordion .header_title {
  background: linear-gradient(to right, 
 rgba(48, 47, 47, 0) 0%, 
 rgba(70, 70, 70, 0.2) 10%, 
 rgba(48, 47, 47, 0) 100%);
 padding: 0 3px;
 position: absolute;
}
.accordion .header_title h2{
  color: whitesmoke;
  padding: 30px 0px 15px 15px;
  text-transform: uppercase;
  /* position: relative;
  left: 3px; */
}

.accordion ul {
  width: 100%;
  display: table;
  table-layout: fixed;
  margin: 0;
  padding: 0;
}
.accordion ul li {
  display: table-cell;
  vertical-align: bottom;
  position: relative;
  width: 16.666%;
  height: 350px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover; 
  transition: all 500ms ease;
}
.accordion ul li div {
  display: block;
  overflow: hidden;
  width: 100%;
}
.accordion ul li div .div_a {
  display: block;
  height: 350px;
  width: 100%;
  position: relative;
  z-index: 3;
  vertical-align: bottom;
  padding: 15px 20px;
  box-sizing: border-box;
  color: #fff;
  text-decoration: none;
  font-family: Open Sans, sans-serif;
  transition: all 200ms ease;
}
.accordion ul li div .div_a * {
  opacity: 0;
  margin: 0;
  width: 100%;
  text-overflow: ellipsis;
  position: relative;
  z-index: 5;
  white-space: nowrap;
  overflow: hidden;
  -webkit-transform: translateX(-20px);
  transform: translateX(-20px);
  transition: all 400ms ease;
}
.accordion ul li div .div_a h2 {
  font-family: Montserrat,sans-serif;
  text-overflow: clip;
  font-size: 24px;
  text-transform: uppercase;
  margin-bottom: 2px;
  top: 16px;
}

.accordion ul li div .div_a  .link_rover:hover {
  color: #d49057;
}

.accordion ul li div .div_a p {
  /* top: 16px; */
  font-size: 13.5px;
}

.accordion ul li div .div_a .row {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  line-height: 2px !important;
}

.accordion ul li div .div_a .row .col{
  
  width: 50%;
}

/* .accordion ul li:nth-child(1) {
  background-image: url("http://michael-ferry.com/assets/accordion1.jpg");
} */
.accordion ul li:nth-child(2) {
  background-image: url("http://michael-ferry.com/assets/accordion2.jpg");
}
.accordion ul li:nth-child(3) {
  background-image: url("http://michael-ferry.com/assets/accordion3.jpg");
}
.accordion ul li:nth-child(4) {
  background-image: url("http://michael-ferry.com/assets/accordion4.jpg");
}
.accordion ul:hover li, .accordion ul:focus-within li {
  width: 25%;
}
.accordion ul li:focus {
  outline: none;
}
.accordion ul:hover li:hover,
.accordion ul li:focus, .accordion ul:focus-within li:focus {
  width: 60%;
}
.accordion ul:hover li:hover .div_a, 
.accordion ul li:focus .div_a, .accordion ul:focus-within li:focus .div_a {
  background: rgba(0, 0, 0, 0.4);
}
.accordion ul:hover li:hover .div_a *,
.accordion ul li:focus .div_a *, .accordion ul:focus-within li:focus .div_a * {
  opacity: 1;
  -webkit-transform: translateX(0);
  transform: translateX(0);
}
.accordion ul:hover li {
  width: 25% !important;
}
.accordion ul:hover li .div_a * {
  opacity: 0 !important;
}
.accordion ul:hover li:hover {
  width: 60% !important;
}
.accordion ul:hover li:hover .header_title {
  display: none;
}
.accordion ul:hover li:hover .div_a {
  background: rgba(0, 0, 0, 0.4);
}
.accordion ul:hover li:hover .div_a * {
  opacity: 1 !important;
  -webkit-transform: translateX(0);
  transform: translateX(0);
}

@media screen and (max-width: 600px) {
  body {
    margin: 0;
  }

  .accordion {
    height: auto;
  }
  .accordion ul li, .accordion ul li:hover, .accordion ul:hover li, .accordion ul:hover li:hover {
    position: relative;
    display: table;
    table-layout: fixed;
    width: 100%;
    transition: none;
  }

  .accordion ul:hover li, .accordion ul:focus-within li {
    width: 100%;
  }

  .accordion ul:hover li:hover,
  .accordion ul li:focus, .accordion ul:focus-within li:focus {
    width: 100%;
  }

  .accordion ul:hover li {
    width: 100% !important;
  }

  .accordion ul:hover li:hover {
    width: 100% !important;
  }

}
